import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import {
  Input,
  Breadcrumb,
  Table,
  Tabs,
  Button,
  message,
  Popconfirm,
  Modal,
  Row,
  Col,
  Form,
  Select,
  Radio
} from "antd";
import styled from "styled-components";
import { injectIntl } from "react-intl";
import {
  listAllUsers,
  createUser,
  updateUser,
  listTypeOfPermits,
  removeUser
} from "./../../services";

const TabPane = Tabs.TabPane;
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;

const ButtonAction = styled(Button)`
  margin: 5px;
`;

class UsersCmp extends React.Component {
  // const {
  //   userLogin,
  //   form: { getFieldDecorator },
  //   intl: { messages }
  // } = props;

  state = {
    confirmDirty: false,
    loading: false,
    sendLoading: false,
    visible: false,
    visibleEdit: false,
    users: [],
    typeOfPermits: [],
    defaultTypeOfPermits: null,
    mode: "add",
    user: {
      fullName: "",
      rol: "user",
      email: "",
      typeOfUser: 3
    }
  };

  componentDidMount = () => {
    this.getUsers();
    this.getTypeOfPermits();
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  showModal = (mode, user = null) => {
    const { userLogin } = this.props;
    const { typeOfPermits } = this.state;
    this.setState({
      mode
    });

    if (mode === "edit") {
      let top = user.TypeOfPermit ? user.TypeOfPermit.id : null;
      if (!top) {
        top = typeOfPermits.length ? typeOfPermits[0].name : null;
      } else {
        if (typeOfPermits.length) {
          top = typeOfPermits.filter(item => {
            return item.id === top;
          });
          if (top.length) {
            top = top[0].name;
          } else {
            top = typeOfPermits[0].name;
          }
        } else {
          top = null;
        }
      }

      this.setState({
        visibleEdit: true,
        user: user,
        defaultTypeOfPermits: top
      });
    } else {
      if (userLogin.rol === "client") {
        let top = userLogin.TypeOfPermit ? userLogin.TypeOfPermit.id : null;
        if (!top) {
          top = typeOfPermits.length ? typeOfPermits[0].name : null;
        } else {
          if (typeOfPermits.length) {
            top = typeOfPermits.filter(item => {
              return item.id === top;
            });
            if (top.length) {
              top = top[0].name;
            } else {
              top = typeOfPermits[0].name;
            }
          } else {
            top = null;
          }
        }

        this.setState({
          visibleEdit: true,
          defaultTypeOfPermits: top,
          user: { fullName: "", rol: "user", email: "", typeOfUser: 3 }
        });
      } else {
        this.setState({
          visibleEdit: true,
          user: { fullName: "", rol: "user", email: "", typeOfUser: 3 }
        });
      }
    }
  };

  hideModal = () => {
    this.setState({
      visible: false,
      visibleEdit: false
    });
    this.handleReset();
    setTimeout(() => {
      this.setState({
        mode: "add"
      });
    }, 1000);
  };

  getUsers = async () => {
    try {
      this.setState({
        loading: true
      });
      const response = await listAllUsers();
      this.setState({
        users: response.data.users,
        loading: false
      });
    } catch (err) {
      message.error(err.message);
      this.setState({
        loading: false
      });
    }
  };

  getTypeOfPermits = async () => {
    try {
      this.setState({
        loading: true
      });
      const response = await listTypeOfPermits();
      let defaultTypeOfPermits = null;

      if (this.props.userLogin.rol !== "client") {
        for (let index = 0; index < response.data.length; index++) {
          if (response.data[index].default) {
            defaultTypeOfPermits = response.data[index].id;
            break;
          }
        }

        this.setState({
          typeOfPermits: response.data,
          defaultTypeOfPermits: defaultTypeOfPermits,
          loading: false
        });
      } else {
        this.setState({
          typeOfPermits: response.data,
          loading: false
        });
      }
    } catch (err) {
      message.error(err.message);
      this.setState({
        loading: false
      });
    }
  };

  handleConfirmBlur = e => {
    const value = e.target.value;
    this.setState({
      confirmDirty: this.state.confirmDirty || !!value
    });
  };

  compareToFirstPassword = (rule, value, callback) => {
    const {
      form,
      intl: { messages }
    } = this.props;
    if (value && value !== form.getFieldValue("password")) {
      callback(messages.signup.rules.password.match);
    } else {
      callback();
    }
  };

  validateToNextPassword = (rule, value, callback) => {
    const { form } = this.props;
    if (value && this.state.confirmDirty) {
      form.validateFields(["confirm"], { force: true });
    }
    callback();
  };

  handleSubmit = e => {
    e.preventDefault();
    const {
      form,
      intl: { messages }
    } = this.props;
    const { mode, user } = this.state;

    form.validateFields((err, values) => {
      if (!err || (mode === "edit" && err.confirm && err.password)) {
        const form = {
          id: user.id || null,
          fullName: values.fullName,
          email: values.email,
          password: values.password,
          rol: values.rol,
          typeOfPermits: values.typeOfPermits,
          typeOfUser: values.typeOfUser
        };
        this.setState({
          sendLoading: true
        });

        if (mode === "add") {
          createUser(form)
            .then(response => {
              message.success(messages.default.success);

              this.setState({
                sendLoading: false
              });
              this.handleReset();
              this.getUsers();
              this.hideModal();
            })
            .catch(error => {
              if (error.response.status === 400) {
                message.error(messages.signup.error.email);
              } else {
                message.error(messages.signup.error.all);
              }
              this.setState({
                sendLoading: false
              });
            });
        } else {
          updateUser(form)
            .then(response => {
              message.success(messages.default.success);
              this.setState({
                sendLoading: false
              });
              this.handleReset();
              this.getUsers();
              this.hideModal();
            })
            .catch(error => {
              if (error.response.status === 400) {
                message.error(messages.signup.error.email);
              } else {
                message.error(messages.signup.error.all);
              }
              this.setState({
                sendLoading: false
              });
            });
        }
      }
    });
  };

  confirmRemoveUser = async id => {
    const {
      intl: { messages }
    } = this.props;
    try {
      await removeUser({ id });
      this.getUsers();
      message.success(messages.default.success);
    } catch (err) {
      message.error(err.message);
    }
  };

  showDashboard = user => {
    const { history } = this.props;
    history.push(`/dashboard/users/${user.rol}/${user.id}`);
  };

  render() {
    const {
      userLogin,
      form: { getFieldDecorator },
      intl: { messages }
    } = this.props;
    const {
      loading,
      users,
      visible,
      sendLoading,
      user,
      mode,
      defaultTypeOfPermits,
      typeOfPermits,
      visibleEdit
    } = this.state;

    const columns = [
      {
        title: messages.signup.fullName,
        dataIndex: "fullName",
        key: "fullName",
        sorter: (a, b) => a.fullName.length - b.fullName.length,
        sortDirections: ["descend"]
      },
      {
        title: "Email",
        dataIndex: "email",
        key: "email"
      },
      {
        title: messages.signup.role,
        dataIndex: "rol",
        key: "rol"
      },
      {
        title: messages.default.actions,
        key: "action",
        render: (text, record) => (
          <span>
            <ButtonAction
              onClick={() => {
                this.showDashboard(record);
              }}
              size="small"
              type="info"
              shape="circle"
              icon="dashboard"
            />
            <ButtonAction
              onClick={() => {
                this.showModal("edit", record);
              }}
              size="small"
              type="primary"
              shape="circle"
              icon="edit"
            />
            <Popconfirm
              onConfirm={() => {
                this.confirmRemoveUser(record.id);
              }}
              title={messages.users.confirmDelete}
              okText={messages.default.yes}
              cancelText={messages.default.no}
            >
              <ButtonAction
                size="small"
                type="danger"
                shape="circle"
                icon="delete"
              />
            </Popconfirm>
          </span>
        )
      }
    ];

    const filterClient = [];
    for (let i = 0; i < users.length; i++) {
      if (users[i].client) {
        if (!filterClient.length) {
          filterClient.push({
            text:
              users[i].client === "---" ? "Without Client" : users[i].client,
            value: users[i].client
          });
        } else {
          let flag = false;
          for (let j = 0; j < filterClient.length; j++) {
            if (filterClient[j].value === users[i].client) {
              flag = true;
              break;
            }
          }
          if (!flag) {
            filterClient.push({
              text: users[i].client,
              value: users[i].client
            });
          }
        }
      }
    }
    const ccolumns = [
      {
        title: messages.signup.fullName,
        dataIndex: "fullName",
        key: "fullName",
        sorter: (a, b) => a.fullName.length - b.fullName.length,
        sortDirections: ["descend"]
      },
      {
        title: messages.process.client,
        dataIndex: "client",
        key: "client",
        filters: filterClient,
        onFilter: (value, record) => record.client.indexOf(value) === 0
      },
      {
        title: "Email",
        dataIndex: "email",
        key: "email"
      },
      {
        title: messages.signup.role,
        dataIndex: "rol",
        key: "rol",
        filters: [
          {
            text: "Admin",
            value: "admin"
          },
          {
            text: "Client",
            value: "client"
          },
          {
            text: "User",
            value: "user"
          }
        ],
        onFilter: (value, record) => record.rol.indexOf(value) === 0
      },
      {
        title: messages.default.actions,
        key: "action",

        render: (text, record) => (
          <span>
            <ButtonAction
              onClick={() => {
                this.showDashboard(record);
              }}
              size="small"
              type="info"
              shape="circle"
              icon="dashboard"
            />
            <ButtonAction
              onClick={() => {
                this.showModal("edit", record);
              }}
              size="small"
              type="primary"
              shape="circle"
              icon="edit"
            />
            <Popconfirm
              onConfirm={() => {
                this.confirmRemoveUser(record.id);
              }}
              title={messages.users.confirmDelete}
              okText={messages.default.yes}
              cancelText={messages.default.no}
            >
              <ButtonAction
                size="small"
                type="danger"
                shape="circle"
                icon="delete"
              />
            </Popconfirm>
          </span>
        )
      }
    ];

    const operations = (
      <Button
        onClick={() => {
          this.showModal("add");
        }}
        type="primary"
        icon="user-add"
      >
        {messages.users.addUser}
      </Button>
    );

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 }
      }
    };

    const radioStyle = {
      display: "block",
      height: "30px",
      lineHeight: "30px"
    };

    let showPass = (
      <div>
        <FormItem label={messages.signup.password} hasFeedback>
          {getFieldDecorator("password", {
            rules: [
              {
                required: true,
                message: messages.signup.rules.password.required
              },
              {
                validator: this.validateToNextPassword
              }
            ]
          })(<Input disabled={mode === "see"} type="password" />)}
        </FormItem>
        <FormItem label={messages.signup.confirm} hasFeedback>
          {getFieldDecorator("confirm", {
            rules: [
              {
                required: true,
                message: messages.signup.rules.password.confirm
              },
              {
                validator: this.compareToFirstPassword
              }
            ]
          })(<Input type="password" onBlur={this.handleConfirmBlur} />)}
        </FormItem>
      </div>
    );
    console.log(userLogin);

    return (
      <div>
        <Breadcrumb style={{ margin: "16px 0" }}>
          <Breadcrumb.Item>{messages.menus.user}</Breadcrumb.Item>
        </Breadcrumb>
        <div style={{ padding: 24, background: "#fff", minHeight: 360 }}>
          <Tabs defaultActiveKey="all" tabBarExtraContent={operations}>
            <TabPane tab={messages.users.all} key="all">
              {userLogin.rol === "admin" ? (
                <Table
                  size="small"
                  loading={loading}
                  dataSource={users}
                  columns={ccolumns}
                />
              ) : (
                <Table
                  size="small"
                  loading={loading}
                  dataSource={users}
                  columns={columns}
                />
              )}
            </TabPane>
          </Tabs>
          <Modal
            centered
            width={600}
            title={messages.users.create}
            visible={visible}
            onOk={this.hideModal}
            onCancel={this.hideModal}
            footer={[
              <Button key="back" onClick={this.hideModal}>
                {messages.default.cancel}
              </Button>,
              <Button
                key="submit"
                type="primary"
                loading={sendLoading}
                onClick={this.handleSubmit}
              >
                {messages.default.create}
              </Button>
            ]}
          >
            <Form {...formItemLayout} onSubmit={this.handleSubmit}>
              <Row justify="center" type="flex">
                <Col xs={24}>
                  <FormItem label={messages.signup.fullName} hasFeedback>
                    {getFieldDecorator("fullName", {
                      initialValue: user.fullName,
                      rules: [
                        {
                          required: true,
                          message: messages.signup.rules.fullName.required
                        }
                      ]
                    })(<Input disabled={mode === "see"} type="text" />)}
                  </FormItem>
                  <FormItem label="E-mail" hasFeedback>
                    {getFieldDecorator("email", {
                      initialValue: user.email,
                      rules: [
                        {
                          type: "email",
                          message: messages.signup.rules.email.type
                        },
                        {
                          required: true,
                          message: messages.signup.rules.email.required
                        }
                      ]
                    })(<Input disabled={mode !== "add"} />)}
                  </FormItem>
                  <FormItem label={messages.signup.role} hasFeedback>
                    {getFieldDecorator("rol", {
                      initialValue: user.rol,
                      rules: [
                        {
                          required: true
                        }
                      ]
                    })(
                      <Select disabled={userLogin.rol === "client"}>
                        <Option value="user">
                          {messages.signup.roles.user}
                        </Option>
                        <Option value="client">
                          {messages.signup.roles.client}
                        </Option>
                        <Option value="admin">
                          {messages.signup.roles.admin}
                        </Option>
                      </Select>
                    )}
                  </FormItem>
                  <FormItem label={messages.signup.typeOfPermits} hasFeedback>
                    {getFieldDecorator("typeOfPermits", {
                      initialValue: defaultTypeOfPermits,
                      rules: [
                        {
                          required: true
                        }
                      ]
                    })(
                      <Select disabled={userLogin.rol === "client"}>
                        {typeOfPermits.map(item => {
                          return (
                            <Option key={item.id} value={item.id}>
                              {item.name}
                            </Option>
                          );
                        })}
                      </Select>
                    )}
                  </FormItem>
                  <FormItem label={messages.signup.typeOfUser}>
                    {getFieldDecorator("typeOfUser", {
                      initialValue: user.typeOfUser,
                      rules: [
                        {
                          required: true
                        }
                      ]
                    })(
                      <RadioGroup disabled={userLogin.rol === "client"}>
                        <Radio style={radioStyle} value={1}>
                          {messages.signup.typeOfUsers.nqnp}
                        </Radio>
                        <Radio style={radioStyle} value={2}>
                          {messages.signup.typeOfUsers.qnp}
                        </Radio>
                        <Radio style={radioStyle} value={3}>
                          {messages.signup.typeOfUsers.qp}
                        </Radio>
                      </RadioGroup>
                    )}
                  </FormItem>

                  {showPass}
                </Col>
              </Row>
            </Form>
          </Modal>
          <Modal
            centered
            width={600}
            title={messages.users.edit}
            visible={visibleEdit}
            onOk={this.hideModal}
            onCancel={this.hideModal}
            footer={[
              <Button key="back" onClick={this.hideModal}>
                {messages.default.cancel}
              </Button>,
              <Button
                key="submit"
                type="primary"
                loading={sendLoading}
                onClick={this.handleSubmit}
              >
                {messages.default.save}
              </Button>
            ]}
          >
            <Form {...formItemLayout} onSubmit={this.handleSubmit}>
              <Row justify="center" type="flex">
                <Col xs={24}>
                  <FormItem label={messages.signup.fullName} hasFeedback>
                    {getFieldDecorator("fullName", {
                      initialValue: user.fullName,
                      rules: [
                        {
                          required: true,
                          message: messages.signup.rules.fullName.required
                        }
                      ]
                    })(<Input disabled={mode === "see"} type="text" />)}
                  </FormItem>
                  <FormItem label="E-mail" hasFeedback>
                    {getFieldDecorator("email", {
                      initialValue: user.email,
                      rules: [
                        {
                          type: "email",
                          message: messages.signup.rules.email.type
                        },
                        {
                          required: true,
                          message: messages.signup.rules.email.required
                        }
                      ]
                    })(<Input disabled={mode !== "add"} />)}
                  </FormItem>
                  <FormItem label={messages.signup.role} hasFeedback>
                    {getFieldDecorator("rol", {
                      initialValue: user.rol,
                      rules: [
                        {
                          required: true
                        }
                      ]
                    })(
                      <Select disabled={userLogin.rol === "client"}>
                        <Option value="user">
                          {messages.signup.roles.user}
                        </Option>
                        <Option value="client">
                          {messages.signup.roles.client}
                        </Option>
                        <Option value="admin">
                          {messages.signup.roles.admin}
                        </Option>
                      </Select>
                    )}
                  </FormItem>
                  <FormItem label={messages.signup.typeOfPermits} hasFeedback>
                    {getFieldDecorator("typeOfPermits", {
                      initialValue: defaultTypeOfPermits,
                      rules: [
                        {
                          required: true
                        }
                      ]
                    })(
                      <Select disabled={userLogin.rol === "client"}>
                        {typeOfPermits.map(item => {
                          return <Option key={item.id}>{item.name}</Option>;
                        })}
                      </Select>
                    )}
                  </FormItem>
                  <FormItem label={messages.signup.typeOfUser}>
                    {getFieldDecorator("typeOfUser", {
                      initialValue: user.typeOfUser,
                      rules: [
                        {
                          required: true
                        }
                      ]
                    })(
                      <RadioGroup disabled={userLogin.rol === "client"}>
                        <Radio style={radioStyle} value={1}>
                          {messages.signup.typeOfUsers.nqnp}
                        </Radio>
                        <Radio style={radioStyle} value={2}>
                          {messages.signup.typeOfUsers.qnp}
                        </Radio>
                        <Radio style={radioStyle} value={3}>
                          {messages.signup.typeOfUsers.qp}
                        </Radio>
                      </RadioGroup>
                    )}
                  </FormItem>
                </Col>
              </Row>
            </Form>
          </Modal>
        </div>
      </div>
    );
  }
}
const WrappedUsersCmp = Form.create()(injectIntl(UsersCmp));

export default withRouter(WrappedUsersCmp);
