import React from "react";
import ReactDOM from "react-dom";
import { Provider } from "react-redux";
import { LocaleProvider, Layout } from "antd";
import { Switch, Route, Redirect } from "react-router-dom";
import { createBrowserHistory } from "history";
import styled from "styled-components";

import { ConnectedRouter } from "connected-react-router";

import enUS from "antd/lib/locale-provider/en_US";

import Process from "./containers/Process";
import Signin from "./containers/Auth/Signin";
import Signup from "./containers/Auth/Signup";
import Dashboard from "./containers/Dashboard";
import PrivateRoute from "./containers/PrivateRoute";
import Download from "./components/Download";
import NotFound from "./components/NotFound";
import IntlWrapper from "./components/IntlWrapper";
import PayCancel from "./components/PayCancel";
import PayError from "./components/PayError";
import PaySuccess from "./components/PaySuccess";
import Frame from "./containers/Frame";

import ContentCmp from "./components/Content";
import HeaderCmp from "./components/Header";

import configureStore from "./store/configureStore";
import * as serviceWorker from "./serviceWorker";

import "ant-design-pro/dist/ant-design-pro.css";
import Background from "./containers/Process/background.jpg";

export const history = createBrowserHistory();

const initialState = {};
const store = configureStore(initialState, history);

const LayoutWrapper = styled(Layout)`
  background-image: url(${Background}) !important;
  background-size: cover !important;
  background-position: center center !important;
`;

ReactDOM.render(
  <Provider store={store}>
    <ConnectedRouter history={history}>
      <LocaleProvider locale={enUS}>
        <IntlWrapper>
          <Switch>
            <Route exact path="/" component={Process} />
            <Route exact path="/process" component={Frame} />

            <Route
              exact
              path="/process/:uuid/success"
              render={() => (
                <LayoutWrapper>
                  <HeaderCmp />
                  <ContentCmp>
                    <PaySuccess />
                  </ContentCmp>
                </LayoutWrapper>
              )}
            />
            <Route
              exact
              path="/process/:uuid/cancel"
              render={() => (
                <LayoutWrapper>
                  <HeaderCmp />
                  <ContentCmp>
                    <PayCancel />
                  </ContentCmp>
                </LayoutWrapper>
              )}
            />
            <Route
              exact
              path="/process/:uuid/error"
              render={() => (
                <LayoutWrapper>
                  <HeaderCmp />
                  <ContentCmp>
                    <PayError />
                  </ContentCmp>
                </LayoutWrapper>
              )}
            />

            <Route exact path="/signin" component={Signin} />
            <Route exact path="/signup" component={Signup} />
            <Route exact path="/download/:uuid" component={Download} />
            <PrivateRoute path="/dashboard" component={Dashboard} />
            <Route exact path="/404" component={NotFound} />
            <Route render={() => <Redirect to="/404" />} />
          </Switch>
        </IntlWrapper>
      </LocaleProvider>
    </ConnectedRouter>
  </Provider>,
  document.getElementById("root")
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
// serviceWorker.unregister();
serviceWorker.register();
